<template lang="pug">  
  .container(v-if="trx")
    h2(v-translate) Transaction
    .secondary
      Trx(
        v-if="trx"
        :trx="trx"
      )
</template>

<script>
import Trx from "@/components/explorer/Trx";
import api from "@/api/explorer";

export default {
  name: "Transaction",
  components: { Trx },
  data() {
    return {
      trx: null
    };
  },
  mounted() {
    if (this.$route.params.transaction)
      api.getTransaction(this.$route.params.transaction).then(res => {
        this.trx = res.data.data;
      });
  }
};
</script>
