<template lang="pug">
  .container.address-viewer
    .tile.is-ancestor
      .tile.is-parent
        article.tile
          .card.tile.is-child.is-address-widget
            .card-content
              h4.level
                .level-left
                  SvgIcon.barcode(name="barcode") 
                  | {{ t('UMI address')}}
              .media.with-margins(v-if="wallet.address")
                .media-left
                  img.qr(@click="toggleModal" :src="'https://chart.googleapis.com/chart?chs=256x256&cht=qr&chl=' + address + '&chf=bg,s,252A32'")
                .media-content {{ wallet.address }}
                .media-right
                  a.button.is-primary(v-clipboard:copy="address" v-clipboard:success="onCopy")
                    SvgIcon(name="content-copy-white")
              .balance.has-text-centered.linear-border(v-if="wallet")
                h2.upper {{ toUmi(realPart * 100) }}
                  span.percents(v-if="fakePart && !wallet.balance.composite_value") {{ fakePart }}
                  SvgIcon.sign(name="sign")
                .is-ultralight ≈ {{ pretty(realPart * 100) }}

      .tile.is-vertical.is-4
        .tile
          .tile.is-parent.is-vertical
            .card.tile.is-child.is-vertical-centered
              .card-content
                .is-ultralight(v-translate) Total sent
                h3
                  growing-number(:value='wallet.sent.value' suffix="UMI")
            .card.tile.is-child.is-vertical-centered
              .card-content
                .is-ultralight(v-translate) Total received
                h3
                  growing-number(:value='wallet.received.value' suffix="UMI")
    
    section.section(v-if="stats.all > 0")
      .container
        .level
          .level-left
            h3.has-text-centered-mobile(v-translate) Transactions
          .level-right
            .level-item
              .tags.are-medium
                .tag(
                  :class="{'is-active' : filter === 'all'}"
                  @click="changeFilter('all')"
                ) {{ t('All') }} ({{ stats.all }})
                .tag(
                  :class="{'is-active' : filter === 'sended'}"
                  @click="stats.sended > 0 ? changeFilter('sended') : null"
                ) {{ t('Sent') }} ({{ stats.sended }})
                .tag(
                  :class="{'is-active' : filter === 'received'}"
                  @click="stats.received > 0 ? changeFilter('received') : null"
                ) {{ t('Received') }} ({{ stats.received }})
        
        Pagination(
          v-if="stats[filter] > params.limit"
          @change="pagination"
          :perPage="params.limit"
          :total="total"
        )

        .transactions(v-if="transactions && transactions.length > 0")
          TrxShort(
            v-if="transactions"
            v-for="(trx, i) in transactions"
            :key="i"
            :trx="trx"
            :address="address"
          )
        
        Pagination(
          v-if="stats[filter] > params.limit"
          @change="pagination"
          :perPage="params.limit"
          :total="total"
        )

    .modal(:class="{'is-active':modalActive}")
      .modal-background(@click="toggleModal")
      .modal-content
         p.image.is-256x256
          img.qr(:src="'https://chart.googleapis.com/chart?chs=256x256&cht=qr&chl=' + address + '&chf=bg,s,FFFFFF'")
      button.modal-close.is-large(aria-label="close" @click="toggleModal")

</template>

<script>
import SvgIcon from "@/components/common/SvgIcon";
import TrxShort from "@/components/explorer/TrxShort";
import GrowingNumber from "@/components/common/GrowingNumber";
import Pagination from "@/components/common/Pagination";
import api from "@/api/explorer";
import { mapGetters } from "vuex";

export default {
  name: "Address",
  components: {
    SvgIcon,
    TrxShort,
    GrowingNumber,
    Pagination
  },
  data() {
    return {
      loading: false,
      wallet: {
        balance: 0,
        received: {
          count: 0,
          value: 0
        },
        sent: {
          count: 0,
          value: 0
        },
        count: 0
      },
      realPart: "",
      fakePart: "",
      transactions: [],
      params: {
        limit: 10,
        offset: 0
      },
      filter: "all",
      interval: null,
      timer: null,
      modalActive: false,
      address: this.$route.params.address
    };
  },
  computed: {
    ...mapGetters(["currency"]),
    coefficient() {
      const effective_interest = this.wallet.balance.confirmed_percent || 0;
      const periods = 30 * 24 * 60 * 60 * 10;
      return (1 + effective_interest / 10000) ** (1 / periods);
    },
    total() {
      if (this.filter === "sended")
        return parseInt(this.wallet.sent.count);
      if (this.filter === "received")
        return parseInt(this.wallet.received.count);
      return this.wallet.count;
    },
    stats() {
      return {
        all: this.wallet.count,
        sended: this.wallet.sent.count,
        received: this.wallet.received.count
      }
    }
  },
  methods: {
    growInit() {
      const start = new Date();
      this.timer = setInterval(() => {
        let time_delta = new Date() - start;
        let value = this.wallet.balance.confirmed_value * this.coefficient ** (time_delta / 100);
        let balance = (value / 100).toFixed(5).split("");
        this.fakePart = "";
        this.realPart = "";
        for (var i = balance.length - 1; i >= 0; i--) {
          if (i > balance.length - 4)
            this.fakePart = balance[i] + this.fakePart;
          else this.realPart = balance[i] + this.realPart;
        }
      }, 100);
    },
    toggleModal() {
      this.modalActive = !this.modalActive;
    },
    changeFilter(type = null) {
      if(this.filter !== type) {
        this.filter = type;
        this.page = 1;
        this.params.offset = 0;
        if(this.$route.query.page) {
          this.$router.push({ query: { page: 1 }})
        }
        this.fetchTransactions();
      }
    },
    pagination(params) {
      if(params.offset > 0) {
        clearInterval(this.interval);
        this.interval = null
      } else if(!this.interval) {
        this.interval = setInterval(() => {
          this.fetchTransactions();
        }, 10000);  
      }
      if(params.offset !== this.params.offset) {
        this.params = params;
        this.fetchTransactions();
      }
      this.params = params;
    },
    fetchTransactions() {
      if(this.address && this.address.length == 62)
        api
          .getAddressTransactions(this.address, this.params, this.filter)
          .then(({ data }) => {
            if(this.transactions.length !== data.data.length || this.wallet.balance.composite_value)
              this.fetchAddress()
            this.transactions = data.data;
          })
          .catch(() => {
            clearInterval(this.timer);
            clearInterval(this.interval);
          })
    },
    fetchAddress() {
      if(this.address && this.address.length == 62)
        api.getAddress(this.address)
          .then(res => {
            this.wallet = res.data.data;
            this.update()
          })
          .catch((err) => {
            console.log(err)
            clearInterval(this.timer);
            clearInterval(this.interval);
            this.$route.push('/')
          })
    },
    update() {
      clearInterval(this.timer)
      if (this.wallet.balance.confirmed_percent && !this.wallet.balance.composite_value) {
        this.growInit();
        //this.realPart = this.wallet.balance.confirmed_value / 100;
      } else {
        this.realPart = this.wallet.balance.confirmed_value / 100;
      }
    },
    fetchData() {
      clearInterval(this.timer);
      clearInterval(this.interval);

      this.fetchAddress();
      this.fetchTransactions();
      this.interval = setInterval(() => this.fetchTransactions(), 10000);  
    }
  },
  mounted() {
    this.fetchData();
  },
  beforeDestroy: function() {
    clearInterval(this.timer);
    clearInterval(this.interval);
  },
  watch: {
    "$route.params.address": {
      handler: function() {
        if(this.address !== this.$route.params.address) {
          this.address = this.$route.params.address
          this.filter = 'all'
          this.realPart = ''
          this.fakePart = ''
          this.transactions = []
          this.params = {
            limit: 10,
            offset: 0
          }
          this.fetchData()
        }
      },
      deep: true,
      immediate: true
    }
  }
};
</script>

<style scoped>
.transactions {
  margin-bottom: 1.5rem;
}
.image.is-256x256 {
  height: 256px;
  width: 256px;
  margin: auto;
}
.percents {
  font-size: 1rem;
  padding-left: 0.35rem;
  padding-right: 0.35rem;
}
.sign {
  height: 2rem;
  width: 2.125rem;
  margin-top: -0.35rem;
  margin-left: 0.35rem;
}
</style>
