<template lang="pug">
  .transaction.secondary(v-if="trx") 
    .columns.is-multiline
      .column.is-narrow
        .line(:class="color ? 'green' : 'red'")
          span.amount(v-if="trx.value && trx.version <= 1")
            | {{ sender ? '+' : '-'}} {{ toUmi(amount) }} UMI
          span(v-else) {{ action }}

          span.is-pulled-right.date.is-ultralight.is-hidden-tablet.is-hidden-desktop.is-hidden-widescreen.is-hidden-fullhd(v-if="trx.confirmed_at") 
            | {{ trx.confirmed_at | moment("DD.MM.YY HH:mm:ss [GMT] Z") }}
      
        .line.icons(v-if="trx.block_height")
          router-link.is-ultralight(:to="{ name: 'Block', params: { height: trx.block_height }}") {{ t('Block') }}: {{ trx.block_height }}
          a.is-ultralight(v-clipboard:copy="$blockchain + 'block/' + trx.block_height" v-clipboard:success="onCopy")
            SvgIcon(name="content-copy")
          a.has-text-link(:href="$blockchain + 'block/' + trx.block_height" target="_blank")
            SvgIcon(name="open-in-new")
        
        .date.is-ultralight.is-hidden-mobile(v-if="trx.confirmed_at")
          | {{ trx.confirmed_at | moment("DD.MM.YY HH:mm:ss [GMT] Z") }}

      .column
        .has-text-right
          .line.icons(v-if="trx.version <=1 || trx.version >= 4")
            SvgIcon.icon(:name="color ? 'green-arrow' : 'red-arrow'")
            span.amount.is-ultralight(v-if="isSmartTrx") {{ toUmi(trx.value) }} UMI
            router-link.address(:to="{ name: 'Address', params: { address: (sender ? trx.sender : trx.recipient) }}")
              | {{ cut(sender ? trx.sender : trx.recipient) }}
            a.is-ultralight(v-clipboard:copy="sender ? trx.sender : trx.recipient" v-clipboard:success="onCopy")
              SvgIcon.icon(name="content-copy")
            a.has-text-link(:href="$blockchain + 'address/' + (sender ? trx.sender : trx.recipient)" target="_blank")
              SvgIcon.icon(name="open-in-new")

          .line.icons(v-if="isSmartTrx && trx.version <= 1")
            SvgIcon.icon(:name="color ? 'green-arrow' : 'red-arrow'")
            span.amount.is-ultralight(v-if="trx.fee_value") {{ toUmi(trx.fee_value) }} UMI
            router-link.address(:to="{ name: 'Address', params: { address: trx.fee_address }}") {{ cut(trx.fee_address) }}
            a.is-ultralight(v-clipboard:copy="trx.fee_address" v-clipboard:success="onCopy")
              SvgIcon.icon(name="content-copy")
            a.has-text-link(:href="$blockchain + 'address/' + trx.fee_address" target="_blank")
              SvgIcon.icon(name="open-in-new")
              
          .icons(v-if="trx.hash")
            SvgIcon.icon(:name="trx.confirmed_at ? 'green-check' : 'red-check'")
            router-link.hash.is-ultralight(:to="{ name: 'Transaction', params: { transaction: trx.hash }}") tx: {{ cut(trx.hash, 15) }}
            a.is-ultralight(v-clipboard:copy="$blockchain + 'transaction/' + trx.hash" v-clipboard:success="onCopy")
              SvgIcon.icon(name="content-copy")
            a.has-text-link(:href="$blockchain + 'transaction/' + trx.hash" target="_blank")
              SvgIcon.icon(name="open-in-new")

</template>

<script>
import SvgIcon from "@/components/common/SvgIcon";

export default {
  name: "TrxShort",
  components: { SvgIcon },
  computed: {
    color() {
      return this.trx.sender != this.address;
    },
    sender() {
      return this.trx.sender != this.address;
    },
    isSmartTrx() {
      return !this.sender && this.trx.fee_address;
    },
    isFeeWallet() {
      return this.address === this.trx.fee_address;
    },
    amount() {
      if(this.isFeeWallet)
        return this.trx.fee_value

      return this.isSmartTrx
        ? parseInt(this.trx.value || 0) + parseInt(this.trx.fee_value || 0)
        : parseInt(this.trx.value || 0);
    },
    action() {
      switch (this.trx.version) {
        case 2:
          return this.$translate.text("Create new structure");
        case 3:
          return this.$translate.text("Update structure");
        case 4:
          return this.$translate.text("Set profit address");
        case 5:
          return this.$translate.text("Set fee address");
        case 6:
          return this.$translate.text("Activate transit address");
        case 7:
          return this.$translate.text("Deactivate transit address");
        default:
          return this.$translate.text("Another operation");
      }
    }
  },
  props: {
    trx: {
      default: null
    },
    address: {
      default: null
    }
  }
};
</script>

<style scoped lang="scss">
.transaction {
  margin-bottom: 0.25rem;
  padding: 1.5rem;

  .line {
    margin-bottom: 1.25rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .icons > *:not(:first-child) {
    margin-left: 0.35rem;
  }
}
.amount {
  white-space: nowrap;
}
.amount,
.address {
  padding-left: 0.35rem;
  text-decoration: none;
}
</style>
