<template lang="pug">  
  .container
    .level
      .level-left
        h2(v-if="block") {{ t('Block height') }} {{ block.height }}
        h2(v-else-if="!loading" v-translate) Block not yet created
      .level-left(v-if="block")
        .is-ultralight {{ block.created_at | moment("DD.MM.YY HH:mm:ss [GMT] Z") }}
    .secondary.block-table.with-underline
      .columns.is-multiline
        .column
          table.table
            tr(v-if="block && block.height")
              td {{ t('Height') }}
              td {{ block.height }}
            tr(v-if="previous")
              td(v-translate) Previous block
              td
                router-link(:to="{ name: 'Block', params: { height: previous }}") {{ previous }}
            tr(v-if="block && block.height && next")
              td(v-translate) Next block
              td
                router-link(:to="{ name: 'Block', params: { height: next }}") {{ next }}
            tr(v-if="block && block.hash")
              td Hash
              td(v-clipboard:copy="block.hash" v-clipboard:success="onCopy")
                .columns.is-flex-mobile.is-vcentered
                  .column
                    .hash {{ block.hash }}
                  .column.is-narrow
                    button.button.is-primary.is-small(type='button' title="Скопировать tx")
                      SvgIcon.icon(name="content-copy-white")
        .column
          table.table(v-if="block && transactions && transactions.length > 0")
            tr
              td(v-translate) Version
              td {{ block.version }}
            tr
              td(v-translate) Transactions
              td {{ transactions.length }}
            tr
              td(v-translate) Total amount
              td {{ toUmi(totalValue) }} UMI / {{ pretty(totalValue) }}
            
    template(v-if="transactions && transactions.length > 0")
      .level
        .level-left
          h2(v-translate) Transactions in block
        .level-left
      .transactions
        Trx(
          v-if="transactions"
          v-for="(trx, i) in transactions"
          :key="i"
          :trx="trx"
        )

</template>

<script>
import api from "@/api/explorer";
import Trx from "@/components/explorer/Trx";
import SvgIcon from "@/components/common/SvgIcon";

export default {
  name: "Block",
  components: { Trx, SvgIcon },
  data() {
    return {
      loading: true,
      block: null,
      previous: null,
      next: null,
      totalValue: null,
      transactions: null
    };
  },
  methods: {
    fetchBlock(height) {
      if (height > 0) this.previous = height - 1;
      this.next = (this.$store.getters.stats.blocks.summary >= (parseInt(height) + 1))
        ? parseInt(height) + 1
        : null
      this.loading = true;

      api
        .getBlock(height)
        .then(res => {
          this.block = res.data.data;
          this.loading = false;
        })
        .catch(() => {
          this.block = null;
          this.loading = false;
        });

      api
        .getBlockTransactions(height)
        .then(res => {
          this.transactions = res.data.data;
          this.totalValue = this.transactions.reduce(
            (acc, trx) => (acc + ((trx.value ? parseInt(trx.value) : 0) + (trx.fee_value ? parseInt(trx.fee_value) : 0))),
            0
          )
        });
    }
  },
  mounted() {
    let height = this.$route.params.height;
    if (height) {
      this.fetchBlock(height);
    }
  },
  watch: {
    "$route.params.height": {
      handler: function(height) {
        this.fetchBlock(height);
      },
      deep: true,
      immediate: true
    }
  }
};
</script>

<style scoped>
.table {
  width: 100%;
}
</style>
